.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: fixed;
  width: 100%;
  padding: 2rem 4rem;
  -webkit-transition: padding 0.37s ease-in;
  -o-transition: padding 0.37s ease-in;
  transition: padding 0.37s ease-in;
  z-index: 2;
}

.header--fixed {
  padding: 1rem 4rem;
  background-color: var(--black);
}

.logo {
  width: 90px;
  -webkit-transition: opacity 0.37s ease-out;
  -o-transition: opacity 0.37s ease-out;
  transition: opacity 0.37s ease-out;
}

.logo:hover {
  opacity: 0.7;
}

.contactMe {
  text-align: center;
}

.contactMe span {
  color: var(--blue-light);
  font-size: 0.8rem;
  font-weight: 300;
}

.contactMe__list {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
}

.contactMe__listItem {
  margin: 0.5rem 0.5rem 0;
}

.contactMe__listItem img {
  width: 1.5rem;
  -webkit-transition: opacity 0.37s ease-out;
  -o-transition: opacity 0.37s ease-out;
  transition: opacity 0.37s ease-out;
}

.contactMe__listItem img:hover {
  opacity: 0.7;
}

@media (max-width: 737px) {
  .header {
    padding: 0.5rem 1rem;
  }

  .logo {
    width: 80px;
  }

  .contactMe__listItem img {
    width: 1.3rem;
  }
}



